@import-normalize;
:root{
  --main-background-color:#f1eee9;
  --black-color:#000000;
  --green-color:#d1ef53;
  --orange-color:#fd620d;
  --gray-color:#e2d1bf;
  --purple-color:#b3a0ce;
  --blue-color:#d1effe;
  --section-padding:80px;
}
@media (min-width:993px) {
  :root{
    --section-padding:80px;
  }
}
@media (max-width:992px) {
  :root{
    --section-padding:40px;
  }
}
@media (max-width:600px) {
  :root{
    --section-padding:15px;
  }
}
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}


body {
  font-family: 'Roboto', sans-serif;
  background-color: var(--main-background-color);
}
a{
  text-decoration: none;
  color: currentColor;
}
/* Start Header */
.header{
  height: 80px;
  padding: 0 var(--section-padding);
  display: flex;
  align-items: center;
  border-bottom: 1px solid var(--black-color);
}
.header--logo{
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 24px;
  font-weight: bold;
  color: var(--black-color);
}
/* End Header */
/* Start Landing */
.landing{
  height: calc(100vh - 80px);
  display: flex;
  border-bottom: 1px solid var(--black-color);
}
.landing >*{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 0 var(--section-padding);
}
.landing--left-side{
  gap: 50px;
  border-right: 1px solid var(--black-color);
  width: 60%;
}
@media (max-width:992px) {
  .landing--left-side{
    width: 50%;
  }
}
.left-side--main-title{
  font-size: 120px;
  font-weight: bold;
  line-height: 0.9;
}
@media (max-width:1600px) {
  .left-side--main-title{
    font-size: 100px;
  }
}
@media (max-width:1200px) {
  .left-side--main-title{
    font-size: 80px;
  }
}
@media (max-width: 992px){
  .left-side--main-title {
    font-size: 60px;
}
}
@media (max-width: 600px){
  .left-side--main-title {
    font-size: 50px;
}
}
.left-side--recipe-of-day{
  display: flex;
  gap: 30px;
  align-items: center;
}
@media (max-width:1024px) {
  .left-side--recipe-of-day{
    flex-direction: column;

  }
}
.recipe-of-day--image{
  width: 300px;
  height: 300px;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  overflow: hidden;
  border: 1px solid var(--black-color);
}
@media (max-width:1200px) {
  .recipe-of-day--image{
    width: 250px;
    height: 220px;
  }
}
@media (max-width:992px) {
  .recipe-of-day--image{
    width: 200px;
    height: 200px;
  }
}
@media (max-width:600px) {
  .recipe-of-day--image{
    width: 180px;
    height: 180px;
  }
}
.recipe-of-day--image img{
  width: 100%;
  height: 100%;
}
.left-side--small-title{
  margin-bottom: 10px;
  display: block;
  font-weight: 500;
}
.recipe-of-day--ingredients{
  position: relative;
  border: 1px solid var(--black-color);
  padding: 30px;
  background-color: var(--main-background-color);
}
@media (max-width:1600px) {
  .recipe-of-day--ingredients{
    padding: 25px;
  }
}
@media (max-width:992px) {
  .recipe-of-day--ingredients{
    padding: 15px;
  }
}
.recipe-of-day--ingredients::before{
  content: "";
  position: absolute;
  right: -8px;
  top: 8px;
  width: 100%;
  height: 100%;
  background-color: var(--black-color);
  z-index: -1;
}
.recipe-of-day--ingredients .recipe-name{
  margin-bottom: 10px;
  display: block;
  font-weight: 500;
}
.recipe-of-day--ingredients .item{
  margin-left: 15px;
}
@media (max-width:1200px) {
  .recipe-of-day--ingredients .item{
   font-size: 14px;
  }
}
.landing--right-side{
  background-color: var(--purple-color);
  gap: 50px;
  width: 40%;
}
@media (max-width:992px) {
  .landing--right-side{
    width: 50%;
  }
}
.right-side--cat{
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: relative;
}
@media (max-width:992px) {
  .right-side--cat{
    justify-content: center;
  }
 
}
.cat--title{
  position: absolute;
  top: 50%;
  transform-origin: left;
  transform: translateY(-50%) translateX(-50%);
  -webkit-transform: translateY(-50%) translateX(-50%);
  -moz-transform: translateY(-50%) translateX(-50%);
  -ms-transform: translateY(-50%) translateX(-50%);
  -o-transform: translateY(-50%) translateX(-50%);
  left: -80px;
  color: var(--black-color);
  font-size: 80px;
  -webkit-text-stroke-width: 3px;
  font-weight: bold;
  display: block;
  cursor: pointer;
  transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
}
@media (max-width:1600px) {
  .cat--title{
    left: -50px;
    font-size: 70px;
  }
}
@media (max-width:1200px) {
  .cat--title{
    left: -50px;
    font-size: 50px;
  }
}
@media (max-width:992px) {
  .cat--title{
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    -moz-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    -o-transform: translate(-50%,-50%);
}
}
@media (max-width:600px) {
  .cat--title{
    font-size: 40px;
}
}
.right-side--cat:first-child .cat--title{
  -webkit-text-stroke-color: var(--gray-color);
  rotate: 4deg;
}
.right-side--cat:first-child .cat--title:hover{
  rotate: -4deg;
}
.right-side--cat:nth-child(2) .cat--title{
  -webkit-text-stroke-color: var(--green-color);
  rotate: -4deg;
}
.right-side--cat:nth-child(2) .cat--title:hover{
  rotate: 4deg;
}
.right-side--cat:last-child .cat--title{
  -webkit-text-stroke-color: var(--orange-color);
  rotate: 4deg;
}
.right-side--cat:last-child .cat--title:hover{
  rotate: -4deg;
}
.cat--image{
  width: 300px;
  height: 200px;
  border: 1px solid var(--black-color);
  overflow: hidden;
}
@media (max-width:1600px) {
  .cat--image{
    width: 250px;
    height: 150px;
  }
}
@media (max-width:600px) {
  .cat--image{
    width: 200px;
    height: 100px;
  }
}
.cat--image img{
  width: 100%;
}
/* End Landing */
.main{
  padding: var(--section-padding);
  border-bottom: 1px solid var(--black-color);
}
/* Start Form */
.recipes-search{
  width: 100%;
  margin-bottom: 40px;
  display: flex;
  justify-content: center;
}
.recipes-search input{
  width: 300px;
  padding: 20px;
  border: 1px solid var(--black-color);
  color: var(--black-color);
  background: none;
  outline: none;
  border-radius: 40px;
  transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  text-align: center;
  font-weight: 600;
  font-size: 16px;
}
.recipes-search input:focus{
  border-color: var(--orange-color);
  width: 500px;
}
/* End Form */
/* Start Categories */
.categories{
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  gap: 10px;
  color: var(--black-color);
  margin-bottom: 40px;
  flex-wrap: wrap;
  row-gap: 50px;
}
.categories--cat{
  transition: all 0.3s ease;
  font-weight: 600;
  padding: 15px 20px;
  border: 1px solid var(--black-color);
  border-radius: 40px;
  -webkit-border-radius: 40px;
  -moz-border-radius: 40px;
  -ms-border-radius: 40px;
  -o-border-radius: 40px;
  width: 150px;
  text-align: center;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
}
.categories--cat.active,
.categories--cat:hover{
  color: var(--orange-color);
  border-color: var(--orange-color);
}
/* End Categories */
/* Start Home Sections */
.home{
  display: flex;
  flex-direction: column;
  gap: 40px;
}
.foods-types-section{
  color: var(--black-color);
}
.foods-types-section--title{
  font-weight: bold;
  font-size: 32px;
  margin-bottom: 20px;
  text-transform: uppercase;
}
.cards--card{
  padding: 30px 60px;
  border: 1px solid var(--black-color);
  width: fit-content;
  text-align: center;
  overflow: hidden;
  position: relative;
  transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  width: 400px;
}
@media (max-width:600px) {
  .cards--card{
    width: 100%;
  }
}
.cards--card:hover{
  background-color: var(--blue-color);
}
.card--title{
  font-weight: 600;
  font-size: 23px;
  margin-bottom: 15px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.card--link-icon{
  position: absolute;
  right: 10px;
  top: 10px;
  font-size: 30px;
  transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
}
.cards--card:hover .card--link-icon{
  color: var(--orange-color);
}
.card--image{
  border: 1px solid var(--black-color);
}
.card--image img{
  max-width: 100%;
}
/* End Home Sections */
/* Start Cuisine */
.cuisine--title{
  font-weight: bold;
  font-size: 32px;
  margin-bottom: 20px;
  text-transform: uppercase;
}
@media (max-width:992px) {
  .cuisine--title{
    text-align: center;
  }
}
.cuisine--cards-grid{
  display: grid;
  grid-template-columns: repeat(auto-fill,minmax(400px,1fr));
  gap: 20px;
  justify-items: center;
}
/* End Cuisine */
/* Start Recipe Page */
.recipe{
  color:  var(--black-color);
}
.recipe--title{
  font-weight: bold;
  font-size: 32px;
  margin-bottom: 20px;
  text-transform: uppercase;
}
.recipe--info{
  display: flex;
  align-items: flex-start;
  gap: 30px;
}
@media (max-width:1200px) {
  .recipe--info{
    flex-direction: column;
  }
}
.recipe--info >*{
  flex: 1;
}
.info--image{
  border: 3px solid var(--black-color);
  border-radius: 6px;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  -ms-border-radius: 6px;
  -o-border-radius: 6px;
  overflow: hidden;
}
.info--image img{
  width: 100%;
}
.info--text{
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.text--btns{
  display: flex;
  align-items: center;
  gap: 20px;
}
.text--btns >*{
  border: 1px solid var(--black-color);
  padding: 15px 40px;
  background: none;
  cursor: pointer;
  font-weight: bold;
  font-size: 22px;
}
.text--btns >*.active{
  background-color: var(--black-color);
  color: white;
}

.info--text .title{
  font-size: 23px;
  font-weight: bold;
  margin-bottom: 15px;
}
.text--instructions-summary p{
  line-height: 1.5;
  font-size: 18px;
}
.text--instructions-summary .text--instructions{
  margin-bottom: 20px;
}
.text--ingredients{
  font-size: 18px;
}
.text--ingredients .ingredients--item{
  margin-bottom: 10px;
  font-weight: normal;
  margin-left: 15px;
}
.similar-recipes{
  margin-top: 40px;
  color: var(--black-color);

}
.similar-recipes--title{
  font-weight: bold;
  font-size: 32px;
  margin-bottom: 20px;
  text-transform: uppercase;
}
.similar-recipes--all-recipes{
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.similar-recipes--all-recipes >*{
  font-size: 23px;
  font-weight: 400;
  width: fit-content;
}
.similar-recipes--all-recipes >*:hover{
  color: var(--orange-color);
}
/* End Recipe Page */